<template>
    <div class="page">
        <div class="title">
            全部
        </div>
        <div class="filteBar">
            <el-input
                size="small"
                placeholder="请输入内容"
                suffix-icon="el-icon-search"
                v-model="input1">
            </el-input>
            <div>
                <el-button type="primary"  size="small">添加资源<i class="el-icon-plus el-icon--right"></i></el-button>
                <el-select v-model="value" size="small" placeholder="时间排序">
                    <el-option>降序</el-option>
                    <el-option>升序</el-option>
                </el-select>
                <el-select v-model="value" size="small" placeholder="不限时间">
                    <el-option>降序</el-option>
                    <el-option>升序</el-option>
                </el-select>
            </div>
        </div>
        <div class="title">
            已有资源
        </div>
        <div v-if="!loading&&experimentList.length==0||!experimentList">
            <el-alert
                title="暂无数据"
                type="info"
                center
                show-icon>
            </el-alert>
        </div>
        <spin :loading="loading"/>
        <div v-for="(item,index) in experimentList" :key="index">
            <a class="item" :href="item.filePath">
                <img src="@/assets/image/file/doc.png" alt="" v-if="item.fileType=='doc'">
                <img src="@/assets/image/file/ppt.png" alt="" v-if="item.fileType=='ppt'">
                <img src="@/assets/image/file/pdf.png" alt="" v-if="item.fileType=='pdf'">
                <span class="description">
                    <h4>{{item.fileName}}</h4>
                    <div>{{item.fileSize||0}}</div>
                    <div>{{item.createTime}}</div>
                </span>
            </a>
        </div>
    </div>
</template>

<script>
import spin from '@/components/spin.vue'
export default {
    props: {

    },
    data() {
        return {
             experimentList: [],
             loading: true
        };
    },
    computed: {

    },
    created() {
        this.$http.get(`/teaching/student/course/resource/${this.$route.params.detailId}`
        ).then((res) => { 
            if(res.data.code == "0"){
                this.courseName = res.data.data.courseName || ""
                this.experimentList = res.data.data || []
            }else if (res.data.code == "1") {
                this.$router.push('/login'); 
            }else{
                this.$message({
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.loading=false
        })
        .catch(function (error) {
            console.log(error)
        })
    },
    mounted() {

    },
    watch: {

    },
    methods: {
    },
    components: {
        spin
    },
};
</script>

<style scoped lang="scss">
.page{
    padding:0 20px;
    .title{
        padding:10px;
        position: relative;
        border-bottom: 1px solid #E9E9E9;
        font-weight: bold;
    }
    .filteBar{
        padding:1em 0;
        display: flex;
        justify-content: space-between;
        .el-input{
            width: 200px;
        }
        .el-select{
            width: 120px;
        }
    }
    .item{
        display: block;
        border-bottom: 1px solid #E9E9E9;
        padding:1em;
        .description{
            display: inline-block;
            line-height: 1.9em;
            margin-left: 0.3em;
            width: 75%;
            h4{
                overflow:hidden;
                text-overflow:ellipsis;
                white-space:nowrap;
            }
            div{
                color:#7F7E7E;
                font-size: 14px;
            }
        }
    }
}
</style>



